<template>
<div class="app-container">
  <!-- Conditional Header -->
  <header class="dashboard-header">
    <div class="logo-greeting">
      <div class="logo">
        <img src="@/assets/lms-Logo.png" alt="LMS Logo" />
      </div>
      <div class="greeting">
        <h1>Welcome, {{ userName }}</h1>
        <p v-if="showHeader">Glad to see you again, ready for another session?</p>
        <p v-else>Great to have you back, ready to inspire and guide the next generation?</p>
      </div>
    </div>
    <nav v-if="showHeader" class="nav-buttons">
      <button @click="navigateTo('/overall')">Overall</button>
      <button @click="navigateTo('/performance')">Performance</button>
      <button @click="navigateTo('/engagement')">Engagement</button>
      <button @click="navigateTo('/admin-dashboard')">Admin</button>
      <button @click="navigateTo('/educator-dashboard')">Educator</button>
    </nav>
  </header>

  <!-- Dynamic Content -->
  <main class="main-content">
    <router-view />
  </main>
</div>
</template>

<script>
import { jwtDecode } from 'jwt-decode'

// LTI Role URIs als Konstanten definieren
const LTI_ROLES = {
  STUDENT: 'http://purl.imsglobal.org/vocab/lis/v2/membership#Learner',
  TEACHER: 'http://purl.imsglobal.org/vocab/lis/v2/membership#Instructor',
  ADMIN: 'http://purl.imsglobal.org/vocab/lis/v2/system/person#Administrator'
}

export default {
  name: 'App',
  data() {
    return {
      userName: 'User',
      roles: []
    }
  },
  computed: {
    showHeader() {
      console.log('[Route Check] Aktuelle Route:', this.$route.path)
      return this.$route.path !== '/educator-dashboard'
    },
    isStudent() {
      const hasRole = this.roles.includes(LTI_ROLES.STUDENT)
      console.log('[Role Check] Ist Student?', hasRole)
      return hasRole
    },
    isTeacher() {
      const hasRole = this.roles.includes(LTI_ROLES.TEACHER)
      console.log('[Role Check] Ist Lehrer?', hasRole)
      return hasRole
    },
    isAdmin() {
      const hasRole = this.roles.includes(LTI_ROLES.ADMIN)
      console.log('[Role Check] Ist Admin?', hasRole)
      return hasRole
    }
  },
  methods: {
    navigateTo(route) {
      console.log('[Navigation] Versuche zu navigieren:', route)
      this.$router.push(route)
    },

    getCookie(name) {
      console.log('[Cookie] Suche nach Cookie:', name)
      const value = `; ${document.cookie}`
      const parts = value.split(`; ${name}=`)
      const cookie = parts.length === 2 ? parts.pop().split(';').shift() : null
      console.log('[Cookie] Gefundener Wert:', cookie ? '*****' : 'Nicht gefunden')
      return cookie
    },

    fetchUserData() {
      console.group('[Auth] Starte Authentifizierungs-Check')
      const token = this.getCookie('auth_token')
      
      if (!token) {
        console.error('[Auth] Kein Token gefunden!')
        console.groupEnd()
        this.$router.push('/login')
        return
      }

      try {
        console.log('[Auth] Versuche Token zu dekodieren...')
        const decoded = jwtDecode(token)
        console.log('[Auth] Dekodiertes Token:', decoded)

        // Rollen aus dem Standard-LTI-Claim holen
        this.roles = decoded['https://purl.imsglobal.org/spec/lti/claim/roles'] || []
        console.log('[Auth] Gefundene Rollen:', this.roles)

        // Benutzernamen mit Fallbacks setzen
        this.userName = decoded.name || decoded.given_name || 'Unbekannter Benutzer'
        console.log('[Auth] Benutzername gesetzt:', this.userName)

        // Automatische Weiterleitung
        console.log('[Routing] Starte rollenbasierte Weiterleitung...')
        this.autoRedirect()
        
      } catch (error) {
        console.error('[Auth] Fehler beim Dekodieren:', error)
        console.groupEnd()
        this.$router.push('/login')
      }
      
      console.groupEnd()
    },

    autoRedirect() {
      const currentPath = this.$route.path
      console.log('[Routing] Aktueller Pfad:', currentPath)

      if (this.isAdmin && !currentPath.startsWith('/admin')) {
        console.log('[Routing] Admin erkannt, leite weiter...')
        this.$router.push('/admin-dashboard')
      } else if (this.isTeacher && !currentPath.startsWith('/educator')) {
        console.log('[Routing] Lehrer erkannt, leite weiter...')
        this.$router.push('/educator-dashboard')
      } else if (this.isStudent && !currentPath.startsWith('/overall')) {
        console.log('[Routing] Student erkannt, leite weiter...')
        this.$router.push('/overall')
      } else {
        console.log('[Routing] Keine Weiterleitung nötig')
      }
    }
  },
  created() {
    console.log('[App] Komponente initialisiert')
    this.fetchUserData()
  },
  watch: {
    $route(to) {
      console.log('[Route] Route hat sich geändert:', to.path)
    }
  }
}
</script>


